.panel-hide-lay[data-v-76943058] {
   width: 100%;
   height: 8vw;
   line-height: 8vw;
   display: block;
   border-radius: 0 0 4vw 4vw;
   /*opacity:0.5;*/
   background: #2f2d2d75;
   filter: Alpha(opacity=50);
   -webkit-filter: Alpha(opacity=50);
   *zoom:1; /* 激活IE6、7的haslayout属性，让它读懂Alpha */
   position: absolute;
   bottom: 0;
   font-size: 5vw;
   color: #fff;
   white-space: nowrap;
   overflow: hidden;
   text-overflow: ellipsis;
}
