html {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  font-style: inherit;
  vertical-align: baseline;
  background: 0 0
}

img {
  max-width: 100%;
  height: auto;
  border-style: none
}

a {
  color: currentColor;
  text-decoration: none
}

ol, ul {
  list-style: none
}

[type=password], [type=range], [type=reset], [type=search], [type=submit], [type=tel], [type=text], [type=time], [type=url], [type=week], [type=button], [type=color], [type=date], [type=datetime-local], [type=email], [type=file], [type=month], [type=number], button, select, textarea {
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none
}

::-webkit-input-placeholder {
  color: currentColor;
  opacity: .5
}

:-moz-placeholder {
  color: currentColor;
  opacity: .5
}

::-moz-placeholder {
  color: currentColor;
  opacity: .5
}

:-ms-input-placeholder {
  color: currentColor;
  opacity: .5
}

table, td, th, tr {
  border-collapse: collapse;
  border-spacing: 0
}

.row {
  display: block;
  width: 100%;
  margin: 0 auto;
  clear: both
}

.row:after, .row:before {
  content: ' ';
  display: table;
  clear: both
}

.row.left {
  float: left
}

.row.right {
  float: right
}

