































































































































.user {
    width: 100%;
    &-poster {
         padding-top: 3vw;
         /*background: cornflowerblue;*/
         background-image:linear-gradient(to right,#b59c88,#806f67);
         color: #fff;
        .poster{
            margin: 3vw 6.5vw;
            width: 18vw;
            height: 18vw;
            img{
                border-radius: 50%;
                width: 100%;
                height: 100%;
            }
        }
        .own-name-line{
            margin-top: 10vw;
            text-align: left;
            .poster-name{
                font-size: 5vw;
                font-weight: 600;
            }
        }
    }

    &-attribute {
        height: 24vw;
        padding: 4vw;
        border-radius: 5vw;
        /*background: #3dcc97;*/
        .own-keep {
            font-size: 6vw;
            color: #1cb5cc;
            height: 10vw;
            line-height: 10vw;
        }
        .own-keep-b{
            font-size: 4vw;
            color: #1cb5cc;
            height: 10vw;
            line-height: 10vw;

        }
    }

    &-group {
        margin-bottom: 15px;
    }
    &-links {
        padding: 15px 0;
        font-size: 12px;
        text-align: center;
        background-color: #fff;
        .van-icon {
            display: block;
            font-size: 24px;
        }
    }
}
